import {
  createContext,
  useCallback,
  useEffect,
  useMemo,
  useState,
} from "react";
import { AccessAuth } from "../services/authRoutes";

export const AuthContext = createContext();

const TOKEN_TODO_APP = "TOKEN_TODO_APP";

export default function AuthContextProvider({ children }) {
  const [isAuthenticated, setIsAuthenticated] = useState(localStorage.getItem(TOKEN_TODO_APP) ? true : false);
  const [user, setUser] = useState(null);

  const authAccess = useCallback(() => {
    const auth = async () => {
      try {
        const res = await AccessAuth(localStorage.getItem(TOKEN_TODO_APP));
        setUser(res.data)
        setIsAuthenticated(true);
      } catch (err) {
        setUser(null)
        setIsAuthenticated(false);
      }
    }

    if (isAuthenticated) auth();
  // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  const logout = () => {
    localStorage.removeItem(TOKEN_TODO_APP);
    setUser(null)
    setIsAuthenticated(false);
  };

  const login = useCallback( async (token) => {
    localStorage.setItem(TOKEN_TODO_APP, token);
    setIsAuthenticated(true);
  }, []);

  const value = useMemo(
    () => ({
      login,
      logout,
      isAuthenticated,
      user,
      setUser
    }),
    [login, isAuthenticated, user]
  );

  useEffect(() => {
    authAccess();
  }, [authAccess]);

  return <AuthContext.Provider value={value}>{children}</AuthContext.Provider>;
}