import * as React from 'react';
import Menu from '@mui/material/Menu';
import MenuItem from '@mui/material/MenuItem';
import Controls from '../../framework/components/Controls';
import { ICON } from '../../framework/components/icons/icons';

export default function MenuList({ id, editState }) {
  const [anchorEl, setAnchorEl] = React.useState(null);
  const open = Boolean(anchorEl);
  const handleClick = (event) => {
    setAnchorEl(event.currentTarget);
  };
  const handleClose = (id_user, state) => {
    setAnchorEl(null);
    if (typeof state !== "string") {
      editState(id_user, state)
    }
  };

  return (
    <div>
      <Controls.ButtonIconComponent
        icon={ICON.CHECK}
        title="Marcar Tarea"
        className="check"
        id="demo-positioned-button"
        aria-controls={open ? 'demo-positioned-menu' : undefined}
        aria-haspopup="true"
        aria-expanded={open ? 'true' : undefined}
        onClick={handleClick}
      />
      <Menu
        id="demo-positioned-menu"
        aria-labelledby="demo-positioned-button"
        anchorEl={anchorEl}
        open={open}
        onClose={handleClose}
        anchorOrigin={{
          vertical: 'top',
          horizontal: 'left',
        }}
        transformOrigin={{
          vertical: 'top',
          horizontal: 'left',
        }}
      >
        <MenuItem onClick={() => handleClose(id, 1)}>Pendiente</MenuItem>
        <MenuItem onClick={() => handleClose(id, 2)}>Cancelado</MenuItem>
        <MenuItem onClick={() => handleClose(id, 4)}>Completado</MenuItem>
      </Menu>
    </div>
  );
}