import React, { useEffect, useState } from "react";
import { Box, Container, Tooltip } from "@mui/material";
import Controls from "../../framework/components/Controls";
import { ICON } from "../../framework/components/icons/icons";
import MenuList from "./MenuList";

export default function HomeTask({ data, eliminarTask, editState, editData }) {
  const [color, setColor] = useState(null);

  useEffect(() => {
    if (data.ESTADO === "Pendiente") setColor("background-green")
    else if (data.ESTADO === "Cancelado") setColor("background-red")
    else if (data.ESTADO === "Incompleto") setColor("background-yellow")
    else if (data.ESTADO === "Completado") setColor("background-secondary")
  }, [data.ESTADO])
  return (
    <Box>
      <Container component="div" style={{ maxWidth: "900px" }}>
        <Box className="list-task task">
          <Tooltip title={`Esta Tarea esta ${data.ESTADO}`} placement="top">
            <Box
              sx={{ width: "15px", height: "15px", borderRadius: "50%" }}
              className={color}
            ></Box>
          </Tooltip>
          <Controls.TextComponent variant="span1" component="span">
            {data.NOMBRE_TAREA}
          </Controls.TextComponent>
          <Box className="list-icon ">
            <MenuList 
              id={data.ID}
              editState={editState}
            />
            <Controls.ButtonIconComponent
              icon={ICON.EDIT}
              title="Editar Tarea"
              className="edit"
              onClick={() => editData(data)}
            />
            <Controls.ButtonIconComponent
              icon={ICON.DELETE}
              className="delete"
              title="Eliminar Tarea"
              onClick={() => eliminarTask(data.ID)}
            />
          </Box>
        </Box>
      </Container>
    </Box>
  );
}
