import React, { useState } from "react";
import Logo from "../../assets/logo.svg"
import Controls from "../../framework/components/Controls";
import { ICON } from "../../framework/components/icons/icons";
import { MessageUtil } from "../../util/MessageUtil";
import { Container, CssBaseline, Paper, Box, Grid } from "@mui/material";
import { useFormValidation } from "../../hooks/useFormValidation";
import "./LoginPage.scss";
import { LoginAuth } from "../../services/authRoutes";
import useAuthContext from "../../hooks/useAuthContext";
import { Link } from "react-router-dom";
import { REGISTER } from "../../config/router/path";

const dataInitial = {
  EMAIL: "",
  PASSWORD: ""
};

export default function LoginPage() {
  // const { login } = useAuthContext();
  const [typeInput, setTypeInput] = useState('password');
  const { login, setUser } = useAuthContext()

  const validate = (fieldValues = data) => {
    let temp = { ...errors };
    if ("EMAIL" in fieldValues) {
      temp.EMAIL = fieldValues.EMAIL === "" ? "El email no debe estar vacio" : "";
    }
    if ("PASSWORD" in fieldValues) {
      temp.PASSWORD = fieldValues.PASSWORD === "" ? "La contraseña esta vacia" : "";
    }
    setErrors({ ...temp });
    if (fieldValues === data) {
      return Object.values(temp).every((x) => x === "");
    }
  };

  const { data, errors, setErrors, handleInputFormChange, resetForm } =
    useFormValidation(dataInitial, true, validate);

  const handleSubmit = async () => {
    if (validate()) {
      try {
        const res = await LoginAuth(data)
        MessageUtil({ 
          message: 'Se ha logueado con éxito',
          type: 'success',
          seg: 10
        })
        login(res.data.token)
        setUser(res.data)
      } catch ({response}) {
        let { status, statusText } = response.data
        resetForm()
        MessageUtil({ 
          message: statusText,
          status,
          type: 'error',
          seg: 5
        })
      }
    }
  }


  return (
    <Box className="background-primary">
      <Container
        component="main"
        maxWidth="xs"
        sx={{ display: "flex", alignItems: "center", minHeight: "100vh", minWidth: "50%" }}
      >
        <CssBaseline />
        <Paper sx={{ width: "100%" }}>
          <Box className="loginContent">
            <Grid container spacing={3}>
              <Grid item xs={12} sx={{ display: "flex", justifyContent: "center" }}>
                <img 
                  src={Logo}
                  alt="Logotipo"
                />
              </Grid>
              <Grid item xs={12}>
                <Controls.InputComponent
                  name="EMAIL"
                  id="email"
                  label="Nombre de Usuario"
                  value={data.EMAIL}
                  onChange={handleInputFormChange}
                  error={errors.EMAIL}
                />
              </Grid>
              <Grid item xs={12}>
                <Controls.InputComponent
                  name="PASSWORD"
                  id="pass"
                  label="Contraseña de Usuario"
                  value={data.PASSWORD}
                  icon={(typeInput === 'password') ? ICON.PASS_INVISIBLE : ICON.PASS_VISIBLE}
                  type={typeInput}
                  onChange={handleInputFormChange}
                  iconClick={(value) => setTypeInput(value)}
                  error={errors.PASSWORD}
                />
              </Grid>
              <Grid item xs={12} sx={{ display: "flex", flexDirection: "column", alignItems: "center", justifyContent: "center" }}>
                <Controls.ButtonComponent
                  title="INICIAR SESIÓN"
                  variant="large"
                  onClick={handleSubmit}
                  style={{ width: "100%" }}
                />
                <br/>
                <Link to={REGISTER}>Crear una cuenta</Link>
              </Grid>
            </Grid>
          </Box>
        </Paper>
      </Container>
    </Box>
  );
}
