import React from "react";
import Logo from "../../assets/logo.svg"
import Controls from "../../framework/components/Controls";
import { MessageUtil } from "../../util/MessageUtil";
import { Container, CssBaseline, Paper, Box, Grid } from "@mui/material";
import { useFormValidation } from "../../hooks/useFormValidation";
import "./LoginPage.scss";
import { Link, useNavigate } from "react-router-dom";
import { LOGIN } from "../../config/router/path";
import { RegisterAuth } from "../../services/authRoutes";

const dataInitial = {
  NAME: "",
  EMAIL: "",
  PASSWORD: "",
  REPEAT_PASS: ""
};

export default function RegisterPage() {
  const validate = (fieldValues = data) => {
    let temp = { ...errors };
    if ("NAME" in fieldValues) {
      temp.NAME = fieldValues.NAME === "" ? "El nombre de usuario es requerido" : "";
    }
    if ("EMAIL" in fieldValues) {
      temp.EMAIL = fieldValues.EMAIL === "" ? "El email de usuario es requerido" : "";
    }
    if ("PASSWORD" in fieldValues) {
      temp.PASSWORD = fieldValues.PASSWORD === "" ? "La constraseña esta vacia" : "";
      temp.REPEAT_PASS = fieldValues.REPEAT_PASS !== fieldValues.PASSWORD ? "La contraseñas no coinciden" : "";
    }
    if ("REPEAT_PASS" in fieldValues) {
      temp.REPEAT_PASS = fieldValues.REPEAT_PASS !== fieldValues.PASSWORD ? "La contraseñas no coinciden" : "";
    }

    setErrors({ ...temp });
    if (fieldValues === data) {
      return Object.values(temp).every((x) => x === "");
    }
  };
  const { data, errors, setErrors, handleInputFormChange, resetForm } = useFormValidation(dataInitial, true, validate);
  const navigate = useNavigate();
  const handleSubmit = async (e) => {
    e.preventDefault()
    if (validate()) {
      try {
        const res = await RegisterAuth(data)
        MessageUtil({ 
          message: res.data,
          type: res.data === "El usuario ya existe" ? 'warning' : 'success',
          seg: 10
        })
        if (res.data !== "El usuario ya existe") return navigate(LOGIN)
      } catch ({ response }) {
        let { status, statusText } = response.data
        resetForm()
        MessageUtil({ 
          message: statusText,
          status,
          type: 'error',
          seg: 5
        })
      } 
    }
  }

  return (
    <Box className="background-primary">
      <Container
        component="main"
        maxWidth="xs"
        sx={{ display: "flex", alignItems: "center", minHeight: "100vh", minWidth: "50%" }}
      >
        <CssBaseline />
        <Paper sx={{ width: "100%" }}>
          <Box className="loginContent">
            <form onSubmit={handleSubmit}>
              <Grid container spacing={3}>
                <Grid item xs={12} sx={{ display: "flex", justifyContent: "center" }}>
                  <img 
                    src={Logo}
                    alt="Logotipo"
                  />
                </Grid>
                <Grid item xs={12}>
                  <Controls.InputComponent
                    name="NAME"
                    label="Nombre de Usuario"
                    value={data.NAME}
                    onChange={handleInputFormChange}
                    error={errors.NAME}
                  />
                </Grid>
                <Grid item xs={12}>
                  <Controls.InputComponent
                    name="EMAIL"
                    type="email"
                    label="Email"
                    value={data.EMAIL}
                    onChange={handleInputFormChange}
                    error={errors.EMAIL}
                    required={true}
                  />
                </Grid>
                <Grid item xs={6}>
                  <Controls.InputComponent
                    name="PASSWORD"
                    label="Contraseña"
                    value={data.PASSWORD}
                    onChange={handleInputFormChange}
                    error={errors.PASSWORD}
                  />
                </Grid>
                <Grid item xs={6}>
                  <Controls.InputComponent
                    name="REPEAT_PASS"
                    label="Repetir Contraseña"
                    value={data.REPEAT_PASS}
                    onChange={handleInputFormChange}
                    error={errors.REPEAT_PASS}
                  />
                </Grid>
                <Grid item xs={12} sx={{ display: "flex", flexDirection: "column", alignItems: "center", justifyContent: "center" }}>
                  <Controls.ButtonComponent
                    type="submit"
                    title="CREAR CUENTA"
                    variant="large"
                    style={{ width: "100%" }}
                  />
                  <br/>
                  <Link to={LOGIN}>Crear una cuenta</Link>
                </Grid>
              </Grid>
            </form>
          </Box>
        </Paper>
      </Container>
    </Box>
  );
}
