import React from 'react';
import { Routes, Route } from 'react-router-dom';
import PrivateRoute from "./PrivateRoute";
import HomeAdminPage from '../module/index/admin/HomeAdminPage';

export default function ModulesRouters () {
  return(
    <Routes>
      <Route path="/*" element={<PrivateRoute path="/" element={<HomeAdminPage />} /> } />
    </Routes>
  )
}