import * as React from "react";
import Dialog from "@mui/material/Dialog";
import DialogActions from "@mui/material/DialogActions";
import DialogContent from "@mui/material/DialogContent";
import DialogTitle from "@mui/material/DialogTitle";
import useMediaQuery from "@mui/material/useMediaQuery";
import { useTheme } from "@mui/material/styles";
import { Box, DialogContentText, Stack, Tooltip } from "@mui/material";
import Controls from "../../framework/components/Controls";
import { ICON } from "../../framework/components/icons/icons";

export default function ResponsiveDialog({
  open,
  setOpen,
  taskIncomplete,
  activeTask,
  eliminarTask
}) {
  const theme = useTheme();
  const fullScreen = useMediaQuery(theme.breakpoints.down("md"));

  const handleClose = () => {
    setOpen(false);
  };

  return (
    <div>
      <Dialog
        fullScreen={fullScreen}
        open={open}
        onClose={handleClose}
        aria-labelledby="responsive-dialog-title"
      >
        <DialogTitle id="responsive-dialog-title">
          {"Estas son tus Tareas Incompletas de esta semana"}
        </DialogTitle>

        <DialogContent sx={{ minWidth: "600px", width: "100%" }}>
          <DialogContentText>
            Al activar tus tareas, se agregaran a la lista de tareas de hoy,
            tambien puedes eliminarlas definitivamente.
          </DialogContentText>
          <br />
          <Stack
            direction="column"
            spacing={2}
            sx={{ padding: "0", margin: "0" }}
          >
            {taskIncomplete &&
              taskIncomplete.map((el, index) => (
                <Box key={index} className="list-task task">
                  <Tooltip
                    title={`Esta Tarea esta ${el.ESTADO}`}
                    placement="top"
                  >
                    <Box
                      sx={{
                        width: "15px",
                        height: "15px",
                        borderRadius: "50%",
                      }}
                      className="background-yellow"
                    ></Box>
                  </Tooltip>
                  <Controls.TextComponent variant="span1" component="span">
                    {el.NOMBRE_TAREA}
                  </Controls.TextComponent>
                  <Box className="list-icon ">
                    <Controls.ButtonIconComponent
                      icon={ICON.CHECK}
                      title="Activar Tarea"
                      className="check"
                      onClick={() => activeTask(el.ID)}
                    />
                    <Controls.ButtonIconComponent
                      icon={ICON.DELETE}
                      className="delete"
                      title="Eliminar Tarea"
                      onClick={() => eliminarTask(el.ID)}
                    />
                  </Box>
                </Box>
              ))}
          </Stack>
        </DialogContent>

        <DialogActions sx={{ display: "flex", justifyContent: "center" }}>
          <Controls.ButtonComponent
            variant="medium"
            type="red"
            title="Cerrar"
            onClick={handleClose}
          />
        </DialogActions>
      </Dialog>
    </div>
  );
}
