import axios from './axios.js';
import { INDEX, INDEX_SAVE, INDEX_UPDATE, INDEX_UPDATE_STATE } from "../config/router/path";

export const indexTask = async (body) => axios.post(INDEX, body);
export const storeTask = async (body) => axios.post(INDEX_SAVE, body);
export const deleteTask = async (id, body) => axios.post(`${INDEX}/${id}`, body);
export const updateTask = async (id, body) => axios.post(`${INDEX_UPDATE}/${id}`, body);
export const updateTaskState = async (id) => axios.put(`${INDEX_UPDATE_STATE}/${id}`);
