import React from 'react';
import { Routes, Route } from 'react-router-dom'
import ModulesRouters from './ModulesRouters';
import PublicRoute from './PublicRoute';
import LoginPage from '../module/auth/LoginPage';
import RegisterPage from '../module/auth/RegisterPage';
import { LOGIN, LOGOUT, REGISTER, HOME } from '../config/router/path';
import PrivateRoute from './PrivateRoute';
import LogoutPage from '../module/auth/LogoutPage';

export default function IndexRouters () {
  return (
    <Routes>
      <Route path="/*" element={<ModulesRouters /> } />
      <Route path={`${HOME}/*`} element={<ModulesRouters /> } />
      <Route path={`${LOGOUT}/*`} element={<PrivateRoute path="/" element={<LogoutPage />} /> } />
      <Route path={`${LOGIN}/*`} element={<PublicRoute path="/" element={<LoginPage />} />} />
      <Route path={`${REGISTER}/*`} element={<PublicRoute path="/" element={<RegisterPage />} /> }/>
    </Routes>
  )
}