import React, { useEffect, useState } from "react";
import { Box, Container, CssBaseline, Grid, Stack } from "@mui/material";
import PrimarySearchAppBar from "../../../components/layout/NavbarComponent";
import Controls from "../../../framework/components/Controls";
import { ICON } from "../../../framework/components/icons/icons";
import { useFormValidation } from "../../../hooks/useFormValidation";
import {
  indexTask,
  storeTask,
  deleteTask,
  updateTask,
  updateTaskState,
} from "../../../services/IndexRoute";
import CardTaskComponent from "../../components/CardTaskComponent";
import "./HomeAdminPage.scss";
import { MessageUtil } from "../../../util/MessageUtil";
import useAuthContext from "../../../hooks/useAuthContext";
import ScrollDialog from "../../components/ModalScroll";

const dataInitial = {
  NOMBRE_TAREA: "",
};

export default function HomeAdminPage() {
  const validate = (fieldValues = data) => {
    let temp = { ...errors };
    if ("NOMBRE_TAREA" in fieldValues) {
      temp.NOMBRE_TAREA =
        fieldValues.NOMBRE_TAREA === ""
          ? "El Tarea no puedes guardar vacio"
          : "";
    }
    setErrors({ ...temp });
    if (fieldValues === data) {
      return Object.values(temp).every((x) => x === "");
    }
  };
  const { data, setData, errors, setErrors, handleInputFormChange, resetForm } =
    useFormValidation(dataInitial, true, validate);
  const [task, setTask] = useState([]);
  const [taskIncomplete, setTaskIncomplete] = useState(0);
  const { user } = useAuthContext();
  const [open, setOpen] = React.useState(false);

  const save = async () => {
    if (validate()) {
      try {
        const res = await storeTask({ ...data, ID_STATE: 1, ID_USER: user.ID });
        getData();
        MessageUtil({
          message: res.data,
          type: "success",
          seg: 10,
        });
        resetForm();
      } catch ({ response }) {
        let { status, statusText, message } = response.data;
        resetForm();
        MessageUtil({
          message: statusText || message,
          status,
          type: "error",
          seg: 5,
        });
      }
    } else {
      MessageUtil({
        message: "La Tarea no puedes guardar vacio",
        type: "warning",
        seg: 10,
      });
    }
  };

  const eliminarTask = async (id) => {
    try {
      const res = await deleteTask(id, { ID_USER: user.ID });
      getData();
      MessageUtil({
        message: res.data,
        type: "success",
        seg: 10,
      });
      resetForm();
      getDataIncomplete();
    } catch ({ response }) {
      let { status, statusText } = response.data;
      resetForm();
      MessageUtil({
        message: statusText,
        status,
        type: "error",
        seg: 5,
      });
    }
  };

  const getData = async (state = 0) => {
    try {
      const res = await indexTask({ EMAIL: user.EMAIL, ESTADO: state });
      setTask(res.data);
    } catch (response) {
      let { status, statusText } = response.data;
      resetForm();
      MessageUtil({
        message: statusText,
        status,
        type: "error",
        seg: 5,
      });
    }
  };

  const getDataIncomplete = async () => {
    try {
      const res = await indexTask({ EMAIL: user.EMAIL, ESTADO: 3 });
      setTaskIncomplete(res.data);
    } catch (response) {
      let { status, statusText } = response.data;
      resetForm();
      MessageUtil({
        message: statusText,
        status,
        type: "error",
        seg: 5,
      });
    }
  };

  const editData = async (data) => {
    setData(data);
  };

  const editState = async (id, state = 0) => {
    try {
      const res = await updateTask(id, {
        ID_USER: user.ID,
        ID: id,
        ID_STATE: state,
        NOMBRE_TAREA: "",
      });
      MessageUtil({
        message: res.data,
        type: "success",
        seg: 10,
      });
      getData();
    } catch ({ response }) {
      let { status, statusText } = response.data;
      MessageUtil({
        message: statusText,
        status,
        type: "error",
        seg: 5,
      });
    }
  };

  const activeTask = async (id) => {
    try {
      const res = await updateTaskState(id);
      MessageUtil({
        message: res.data.message,
        type: "success",
        seg: 10,
      });
      getDataIncomplete();
      getData();
    } catch ({ response }) {
      let { status, statusText } = response.data;
      MessageUtil({
        message: statusText,
        status,
        type: "error",
        seg: 5,
      });
    }
  };

  const handleClickModal = () => {
    setOpen(true);
  };

  useEffect(() => {
    if (user) getData();
    if (user) getDataIncomplete();

    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [user]);

  useEffect(() => {}, []);

  return (
    <Box className="main">
      <CssBaseline />

      <PrimarySearchAppBar
        taskIncomplete={taskIncomplete.length || 0}
        handleClickModal={handleClickModal}
      />

      <Container
        component="main"
        maxWidth="100%"
        sx={{ paddingTop: "15px", paddingBottom: "15px", minHeight: "100%" }}
      >
        <Grid container spacing={2} alignItems="center">
          <Grid item xs={12} sm={12} md={8} lg={8} xl={8}>
            <Grid container spacing={1} alignItems="center">
              <Grid item xs={12} sm={3} lg={3}>
                <Box
                  className="background-third"
                  sx={{
                    padding: "5px",
                    borderRadius: "5px",
                    textAlign: "center",
                  }}
                >
                  <Controls.TextComponent variant="subtitle3" component="span">
                    {new Date().toLocaleDateString("es-ES", {
                      year: "numeric",
                      month: "long",
                      day: "numeric",
                    })}
                  </Controls.TextComponent>
                </Box>
              </Grid>

              <Grid item xs={12} sm={3} lg={3}>
                <Controls.ButtonComponent
                  title="Actividades Cumplidas"
                  variant="small"
                  type="green"
                  style={{ width: "100%" }}
                  onClick={() => getData(4)}
                />
              </Grid>

              <Grid item xs={12} sm={3} lg={3}>
                <Controls.ButtonComponent
                  title="Actividades Canceladas"
                  variant="small"
                  type="red"
                  style={{ width: "100%" }}
                  onClick={() => getData(2)}
                />
              </Grid>

              <Grid item xs={12} sm={3} lg={3}>
                <Controls.ButtonComponent
                  title="Actividades Incompletas"
                  variant="small"
                  type="yellow"
                  style={{ width: "100%" }}
                  onClick={() => getData(3)}
                />
              </Grid>
            </Grid>
          </Grid>

          <Grid
            item
            xs={12}
            sm={12}
            md={4}
            lg={4}
            xl={4}
            sx={{ display: "flex", justifyContent: "flex-end" }}
          >
            <Controls.ButtonComponent
              variant="large"
              title="Restablecer"
              onClick={() => {
                getData();
                getDataIncomplete();
              }}
            />
          </Grid>
        </Grid>
        <br />
        <Box>
          <Controls.TextComponent
            variant="h2"
            component="h2"
            style={{ textAlign: "center" }}
          >
            {data.ID ? "Actualizar Tarea" : "Agregar nueva Tarea"}
          </Controls.TextComponent>
        </Box>
        <br />
        <Box>
          <Container component="div" style={{ maxWidth: "900px" }}>
            <Box className="list-task">
              <Controls.InputComponent
                className="input-change"
                placeholder="Escribe aquí tu Nueva Tarea"
                name="NOMBRE_TAREA"
                value={data.NOMBRE_TAREA}
                onChange={handleInputFormChange}
              />
              <Box className="list-icon">
                <Controls.ButtonIconComponent
                  icon={ICON.SAVE}
                  title={data.ID ? "Actualizar" : "Guardar"}
                  className="check"
                  onClick={save}
                />
                <Controls.ButtonIconComponent
                  icon={ICON.CLOSE_CIRCLE}
                  title="Borrar"
                  className="delete"
                  onClick={resetForm}
                />
              </Box>
            </Box>
          </Container>
        </Box>
        <br />
        <Box>
          <Controls.TextComponent
            variant="h2"
            component="h2"
            style={{ textAlign: "center" }}
          >
            Lista de Tareas
          </Controls.TextComponent>
        </Box>
        <br />
        <Stack direction="column" spacing={2}>
          {task &&
            task.map((el, index) => (
              <CardTaskComponent
                key={index}
                data={el}
                editData={editData}
                eliminarTask={eliminarTask}
                editState={editState}
              />
            ))}
        </Stack>
      </Container>

      <ScrollDialog
        open={open}
        setOpen={setOpen}
        taskIncomplete={taskIncomplete}
        activeTask={activeTask}
        eliminarTask={eliminarTask}
        getDataIncomplete={getDataIncomplete}
      />
    </Box>
  );
}
